import { customElement, property, state } from 'lit/decorators.js';
import { classMap } from 'lit/directives/class-map.js';
import { html, TemplateResult } from 'lit';
import { unsafeHTML } from 'lit/directives/unsafe-html.js';

import { Template } from '../base';
import '../shared/zwc-template';

export type BreakerData = {
  id: string;
  image: string;
  preline: string;
  title: string;
};

@customElement('zwc-breaker')
export class ZwcBreaker extends Template {
  @property({ type: String })
  title = 'Breaker title';

  @property({ type: Boolean, attribute: 'show-title' })
  showTitle = false;

  @property({ type: Boolean, attribute: 'align-right' })
  alignRight = false;

  /** data will receive HTML string from zwp-template */
  @state()
  data = '';

  render(): TemplateResult {
    const classes = {
      breaker: true,
      'breaker--right': this.alignRight,
    };

    return html`
      <aside class=${classMap(classes)}>
        ${this.showTitle
          ? html`<p class="breaker-title">${this.title}</p>`
          : ''}
        ${unsafeHTML(this.data)}
      </aside>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-breaker': ZwcBreaker;
  }
}
